.promo-block {
  position: relative;
}

.promo-block__inner {
  padding-right: 25px;
  padding-left: 25px;
  padding-bottom: 89px;

  max-width: 1411px;
  margin: 0 auto;

  color: $white;

  position: relative;

  @media (max-width: $tablet-width) {
    padding-left: 88px;
    padding-bottom: 177px;
  }

  @media (max-width: $mobile-width) {
    padding-left: 15px;
    padding-right: 15px;
    padding-bottom: 100px;
  }
}

.promo-block__inner::before {
    content: "";
    position: absolute;
    top: 173px;
    right: 0;
    z-index: -10;
    background-image: url("../img/schema-desktop@1x.png");
    
    width: 705px;
    height: 515px;
    background-size: cover;
    background-repeat: no-repeat;

    @media (min-resolution: $retina-dppx) and (min-resolution: $retina-dpi) {
      background-image: url("../img/schema-desktop@2x.png");

      @media (max-width: $tablet-width) {
        background-image: url("../img/schema-tablet@2x.png");
      }

      @media (max-width: $mobile-width) {
        background-image: url("../img/schema-mobile@2x.png");
      }
    }

  @media (max-width: $tablet-width) {
    width: 310px;
    background-image: url("../img/schema-tablet@1x.png");
    top: -50px;
  }

  @media (max-width: $mobile-width) {
    background-image: url("../img/schema-mobile@1x.png");
    background-size: contain;
    width: 294px;
    top: -59px;
  }
}

.promo-block::before {
  content: "";
  position: absolute;
  top: -89px;
  z-index: -10;
  background-image: url("../img/header-bg-desktop@1x.jpg");
  background-size: cover;
  width: 100%;
  height: 878px;
  background-repeat: no-repeat;
  background-color: $dark-blue;

  @media (min-resolution: $retina-dppx) and (min-resolution: $retina-dpi) {
    background-image: url("../img/header-bg-desktop@2x.jpg");

    @media (max-width: $tablet-width) {
      background-image: url("../img/header-bg-tablet@2x.jpg");
    }

    @media (max-width: $mobile-width) {
      background-image: url("../img/header-bg-mobile@2x.jpg");
    }
  }

  @media (max-width: $tablet-width) {
    background-image: url("../img/header-bg-tablet@1x.jpg");
    height: 692px;
    top: -76px;
  }

  @media (max-width: $mobile-width) {
    background-image: url("../img/header-bg-mobile@1x.jpg");
    height: 658px;
    top: -59px;
  }
}

.promo-block__title {
  font-family: "RobotoCondensed", "Arial", sans-serif;
  font-size: 80px;
  line-height: 94px;

  letter-spacing: 0.07em;
  text-transform: uppercase;

  margin: 0;
  padding-bottom: 31px;
  padding-top: 231px;

  @media (max-width: $tablet-width) {
    font-size: 60px;
    line-height: 70px;
    max-width: 350px;
    padding-top: 131px;
  }

  @media (max-width: $mobile-width) {
    padding-top: 231px;
    font-size: 40px;
    line-height: 47px;
  }
}

.promo-block__text {
  position: relative;
  padding-left: 94px;
  padding-bottom: 37px;

  font-size: 22px;
  line-height: 34px;

  margin: 0;
  width: 629px;

  @media (max-width: $tablet-width) {
    font-size: 18px;
    padding-left: 48px;
    max-width: 404px;
  }

  @media (max-width: $mobile-width) {
    padding-right: 0;
    font-size: 16px;
    width: auto;
  }
  
}

.promo-block__text::before {
  content: "";
  position: absolute;
  top: 15px;
  left: 0;
  width: 66px;
  height: 2px;
  background-color: $white;

  @media (max-width: $tablet-width) {
    width: 33px;
  }
}

.promo-block__button {
  display: block;
  width: 407px;
  border: none;
  color: $white;
  background-color: $blue;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: 0.05em;

  margin-left: 94px;

  @media (max-width: $tablet-width) {
    margin-left: 48px;
    width: 379px;
  }

  @media (max-width: $mobile-width) {
    width: 100%;
    margin: 0 auto;
  }
}

.promo-block__scroll {
  transform: rotate(180deg);
  writing-mode: vertical-rl;
  padding-bottom: 93px;
  text-decoration: none;
  color: $white;
  opacity: 0.5;
  font-size: 14px;
  line-height: 16px;
  cursor: pointer;

  @media (max-width: $tablet-width) {
    display: none;
  }
}

.promo-block__button-text--mobile {
  display: none;

  @media (max-width: $mobile-width) {
    display: inline;
  }
}

.promo-block__button-text--desktop {
  @media (max-width: $mobile-width) {
    display: none;
  }
}
